.header_container {
    height: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.menu_container_box {
    flex: 1;
    display: flex;
}

.header_icon_box {
    display: flex;
    align-items: center;
}

.header_icon_container {
    padding: 5px;
}

.menu_button_container {
    border-right: 1px solid rgba(199, 199, 199, 0.61);
    padding: 10px;
}

.header_seperator {
    height: 10px;
    width: 100%;
    background-color: #F9F7F7;
}