.main_container {
  height: 100vh;
  width: 100vw;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.wrapper {
  flex: 1;
  overflow-y: auto;
}

.input {
  width: 100%;
  height: 50px;
  text-decoration: none;
  cursor: pointer;
  border: none;
  background-color: #f9f7f7;
  padding-left: 10px;
  border-left: 5px solid #ff0000;
}
.input_container {
  margin-left: 10px;
  margin-top: 20px;
}
.color:hover {
  border: 1px solid blue;
}
.color {
  height: 43px;
  width: 3px;
  background-color: #ff0000;
  margin-left: -3px;
  margin-bottom: -43px;
  cursor: pointer;
}
.main_container_for_contact_us {
  font-size: 20px;
  font-family: Roboto-Bold;
}
.contact_us_box {
  width: 15px;
  height: 15px;
  background-color: #ff0000;
  margin-right: 5px;
}
.contact_us_container {
  display: flex;
  margin-left: 10px;
  align-items: center;
}
.submit_button {
  justify-content: center;
  display: flex;
  align-items: center;
}
.button {
  background-color: #ff0000;
  width: 100px;
  height: 40px;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 20px;
}
.button:hover {
  background-color: #f80909;
}
.footer_top_border {
  background-color: #ff0000;
  height: 3px;
  width: 100%;
  margin-top: 30px;
}
.download_pm_news_app {
  justify-content: center;
  align-items: center;
  display: flex;
  margin-top: 20px;
}
.google_play_ads {
  justify-content: center;
  align-items: center;
  display: flex;
}
.footer_pics {
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  display: flex;
}
.follow_pm_news {
  justify-content: center;
  align-items: center;
  display: flex;
  margin-top: 20px;
}
.social_media {
  justify-content: center;
  align-items: center;
  display: flex;
  margin-top: 10px;
  margin-bottom: 10px;
}
.footer {
  width: 100%;
  background-color: #ebe9e9;
  padding: 10px;
}
.row {
  display: flex;
  justify-content: space-between;
  width: 435px;
  margin: auto;
  margin-top: 10px;
}
.footer_down_border {
  height: 30px;
  width: 100%;
  background-color: #a8a4a4b9;
}
